body {
  margin: 0;
  padding: 0;
  font-family: 'Didot' sans-serif;
  background-color: #FDFEFE;
}

#main h1{
  text-align: center;
  color: rgb(31, 78, 78);
}

#main{
  padding: 0px;
  box-sizing: border-box;
  width:60%;
  height:100%;
}
#book-list{
  padding: 0;
}

#book-list li{
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius:4px;
  border: 1px solid #06534d;
  box-shadow: 1px 2px 3px rgba(0,0,0, 0.3); 
  cursor: pointer;
  color: rgb(19, 36, 36);
}

form{
  background: rgb(172, 202, 202);
  padding: 20px;
  position: fixed;
  left:0;
  bottom:0px;
  width: 400px;
}
 form .field{
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
   grid-gap: 10px;
 }

 form label{
   text-align: right;
   padding: 6px;
 }

 form input, form select{
   margin: 4px 0;
   padding: 6px;
   box-sizing: border-box;
 }

 form button{
   color:#FDFEFE;
   font-size: 2.2rem;
   background: #02221f;
   border: 0;
   padding: 0 5px;
   border-radius: 4px;
   cursor: pointer;
   bottom: 10px;
   left:40px;
 }

 #book-details{
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  background: #288880;
  padding: 30px;
  overflow: auto;
  box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
  box-sizing: border-box;
  color: #FDFEFE;
}